export default{
	data(){
		return{
			vipAmount:"99",
			vipMaskNum:"50"
		}
	},
	onLoad(opt){
		if(opt.vipAmount && opt.vipAmount != "undefined"){
			this.vipAmount = opt.vipAmount;
		}
		if(opt.vipMaskNum && opt.vipMaskNum != "undefined"){
			this.vipMaskNum = opt.vipMaskNum;
		}
	}
}
